import React from 'react';
import Button from '@material-ui/core/Button';
import firebase from 'firebase';
import 'firebase/firestore';
import { firestore } from './firebase';

export default class Battle extends React.Component {

  constructor(){
    super();
    this.state = {
      battle:false,
      winner:null
    };
    this.compare=this.compare.bind(this);
    this.resetScan=this.resetScan.bind(this);
  }

  propTypes: {
  }

  compare() {

    const result1 = this.props.result1[0].codeResult.code;
    const result2 = this.props.result2[0].codeResult.code;
    const total1 = this.props.total1;
    const total2 = this.props.total2;
    const name1 = this.props.name1;
    const name2 = this.props.name2;

    console.log("result1:"+result1+" result2:"+result2);
    console.log(name1 + ":" + total1);
    console.log(name2 + ":" + total2);

    let winner = null;
    let winnerCode = null;

    if(total1-total2 < 0){
      winner = name2;
      winnerCode = result2;
    }else if(total1-total2 == 0){
      winner = "TIE";
    }else{
      winner = name1;
      winnerCode = result1;
    }

    this.setState({
        battle: true,
        winner: winner
      });

    console.log("looking for code "+winnerCode);

    firestore.collection('barcodes').doc(winnerCode)
    .get()
    .then(doc=>{
      console.log(doc.id,':',doc.data().wins);
      let winsTemp = 0;
      if(doc.data().wins==null) winsTemp=0;
      else winsTemp = doc.data().wins;

      console.log("winsTemp:"+winsTemp);

      //register to firestore
      firestore.collection("barcodes").doc(winnerCode).update({
          wins:winsTemp+1
        });
      });

  };

  resetScan(){
    this.props.resetScan();
  }

  componentWillUnmount(){
    this.setState({
      battle: false,
      winner: null
    });
  }

  render() {

    return (
      <div>
        {!this.state.battle ? <Button variant='contained' color='secondary' onClick={this.compare} fullWidth='true'>BATTLE</Button> : null}
        {this.state.battle ? <h1>{this.state.winner}</h1> : null}
        {this.state.battle ? <Button variant='contained' color='secondary' onClick={this.resetScan} size='small'>NEXT BATTLE</Button> : null}
      </div>
    );
  }
};
