import React from 'react';
import Scanner from './Scanner';
import Result from './Result';
import Battle from './Battle';
import Ranking from './Ranking';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Modal from '@material-ui/core/Modal';

const scanBtn = {
    left: '30%',
    marginTop: '40px'
  };

export default class App extends React.Component {
    constructor(){
      super();
      this.state = {
        scanning1: false,
        scanned1:false,
        results1: [],
        scanning2: false,
        scanned2:false,
        results2:[],
        total1:null,
        total2:null,
        name1:null,
        name2:null,
        table:false
      };
      this._scan1=this._scan1.bind(this);
      this._scan2=this._scan2.bind(this);
      this._onDetected1=this._onDetected1.bind(this);
      this._onDetected2=this._onDetected2.bind(this);
      this.registerTotal=this.registerTotal.bind(this);
      this.resetScan=this.resetScan.bind(this);
      this.showTable=this.showTable.bind(this);
    }

    render() {
        return (
          <div>
            <AppBar position="static" color="default">
              <Toolbar>
                <IconButton color="inherit" aria-label="Menu" onClick={this.showTable}>
                  <MenuIcon />
                  </IconButton>
                  <Typography variant="h6" color="inherit">
                    Barcode Battler
                  </Typography>
                </Toolbar>
              </AppBar>
            <div>
              {this.state.table? <Ranking /> : null}
            </div>
            <div id="scan-area">
                <Button variant='contained' color='primary' size='large' onClick={this._scan1} style={scanBtn}>{this.state.scanning1 ? 'Stop' : 'SCAN1'}</Button>
                {this.state.results1.map((result) => (<Result key={result.codeResult.code} player={1} result={result} registerTotal={this.registerTotal}/>))}
                {this.state.scanning1 ? <Scanner onDetected={this._onDetected1} /> : null}
            </div>
            <div id="scan-area">
              <Button variant='contained' color='primary' size='large' onClick={this._scan2} style={scanBtn}>{this.state.scanning2 ? 'Stop' : 'SCAN2'}</Button>
              {this.state.results2.map((result) => (<Result key={result.codeResult.code} player={2} result={result} registerTotal={this.registerTotal}/>))}
              {this.state.scanning2 ? <Scanner onDetected={this._onDetected2} /> : null}
            </div>
            <div>
              {this.state.scanned1&&this.state.scanned2 ? <Battle result1={this.state.results1} result2={this.state.results2} total1={this.state.total1} total2={this.state.total2} name1={this.state.name1} name2={this.state.name2} resetScan={this.resetScan}/> : null}
            </div>
          </div>
        );
    };

    _scan1() {
        this.setState({
          scanning1: !this.state.scanning1,
          results1:[]
        });
      };

      _scan2() {
          this.setState({
            scanning2: !this.state.scanning2,
            results2:[]
          });
        };

    _onDetected1(result) {
        //this.setState({results: this.state.results.concat([result])});
        this.setState({
          results1: [result],
          scanned1:true
        });
    };

    _onDetected2(result) {
        //this.setState({results: this.state.results.concat([result])});
        this.setState({
          results2: [result],
          scanned2: true
        });
    };

    registerTotal(value,player, name) {
        if(player==1&&this.state.scanning1!=false){
          this.setState({
            scanning1: false,
            total1:value,
            name1:name
          });
        }
        else if(player==2&&this.state.scanning2!=false){
          this.setState({
            scanning2: false,
            total2:value,
            name2:name
          });
        }
    };

    resetScan(){
      this.setState({
        results1: [],
        scanned1: false,
        results2: [],
        scanned2: false,
        name1:null,
        name2:null,
        total1:0,
        total2:0
      });
    }

    showTable(){
      this.setState({ table: !this.state.table });
    }
};
