import React from 'react';
import {Radar} from 'react-chartjs-2'

export default class MonsterStats extends React.Component {

  constructor(){
    super();
    this.state = {
    };
  }

  propTypes: {
  }

  render() {

    const data = {
      labels: ['Price', 'JAN Digits', 'Yahoo Shopping Hits'],
      datasets: [
        {
          label: this.props.name,
          backgroundColor: 'rgba(255,99,132,0.2)',
          borderColor: 'rgba(255,99,132,1)',
          pointBackgroundColor: 'rgba(255,99,132,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(255,99,132,1)',
          data: [this.props.price, this.props.code.toString().length, this.props.hits]
        }
      ]
    };

    return(
      <div>
        <Radar data={data} width={200} height={200} options={{maintainAspectRatio: false, legend: {display: false}}} />
      </div>
    );
  }
};
